
.audio-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap-reverse;
      flex-wrap: wrap-reverse;
  height: 100px;
  overflow: hidden;
}
.bar {
  margin-top: 100px;
  margin-left: 3px;
  background: #1db954;
  height: 100px;
  width: 2.5vw;
  -webkit-animation: sound 0ms -800ms linear infinite alternate;
          animation: sound 0ms -800ms linear infinite alternate;
  pointer-events: none;
}
@-webkit-keyframes sound {
0% {
      opacity: .8;
      min-height: 0.3vh;
      max-height: 1vh;
}
100% {
      opacity: 1;
      min-height: 8vh;
      max-height: 10vh;
}
}
@keyframes sound {
0% {
      opacity: .8;
      min-height: 0.3vh;
      max-height: 1vh;
}
100% {
      opacity: 1;
      min-height: 8vh;
      max-height: 10vh;
}
}



